<script>
  //contentType 1 video   2 live  3book  4 pic
  import {mapActions} from 'vuex' 

  
  
  export default {
  	name:'home',

  	data() {
      return {
       dialog:false,
       canload:true,
       open:true,
       docked:true,
       loading:false,
       scroller: null,
       list:[],
       page:1,
       apitype:"korean",
       title:"美女直播",
       contentType:1,
       screenWidth:0,
     }
   },
   mounted() {
    
     this.scroller=window
     this.screenWidth=window.width;
     this.loadMore()
   },
   computed:{
    msg(){
      return this.$store.state.test
    },
    logined(){
      return this.$store.state.loginedUser
    }

  },
  beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.canload=true
      })
    },
  beforeRouteLeave(to,from,next)
  { 

    if(!localStorage.getItem("canView")){
      this.$router.push({path:"/login"})
    }
    else {
      next(true)
      this.canload=false;
    }
},

methods: {
  toggle (flag) {
    this.open=!this.open
    this.docked=!flag

  },
  loadMore(added=true){
   
    if(!this.canload){
      return 
    }
    
    this.loading=true
    this.$http.get(this.$store.state.serverUrl+"/"+this.apitype+"/page/"+this.page,{headers:{"Authorization":"test"}}).then(response=>{
     if(added){
     
        this.list=this.list.concat(response.body)
      
    }
    else {
      this.list=response.body

    }
    this.loading=false
    this.page++
  },response=>{
    this.list=[]
  })
  },
  closeTip(){
    this.dialog=false
    this.open=false
  },
  gotobook(item){
   
    this.changebook(item)
    this.$router.push({path:'/book'})
  },
  gotoVipVideo(item)
  {
    this.changeVipVideo(item)
    this.$router.push({path:"/vipvideo"})
  },
  gotomm(item){
   let id=item.id
   let pages=item.page
   let pics=[]
   for (var i=1;i<=pages;i++){
    pics.push("http://img1.mm131.com/pic/"+id+"/"+i+".jpg")

  }
  this.changemm({title:item.title,images:pics})
  this.$router.push({path:'/picture'})
},
goto942Video(item)
{   
    let da={}
    da['poster']='https://www.942porn.com/media/videos/tmb/'+item.sid+"/1.jpg"
    let pics=[]
    for(var i =1;i<20;i++){
      pics.push('https://www.942porn.com/media/videos/tmb/'+item.sid+"/"+i+".jpg")
    }
    da['photos']=pics
    da['mp4']="https:"+item.url
    da['title']=item.title
    this.changevideo(da);
  this.$router.push({path:"/video"})

},
gotoLiveVideo(item)
{ 
  
  this.changeLiveVideo(item)
  this.$router.push({path:"/livevideo"})
},
gotovideo(item)
{ 
  if(item['poster'].indexOf("dmm.com")>0){
   let size=item['photonum']
   let pics=[]
   for (var i = 1; i <=size; i++) {
    pics.push({"url":"https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"jp-"+i+".jpg"})
  }
  item['poster']="https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"ps.jpg"
  item['photos']=pics
  
}
this.changevideo(item);
this.$router.push({path:"/video"})
},
...mapActions(['changevideo','changemm','changebook','changeVipVideo','changeLiveVideo','changeUser']),
refresh () {
  this.refreshing=true
  setTimeout(()=>{this.refreshing=false},5000)
},
tologin(){
  this.$router.push({path:"/login"})
},
loginout()
{
  sessionStorage.clear()
  this.changeUser(null)
  alert("您已成功推出当前账号")
  window.location="/"
},
itemclick(tp){
  this.list=[]
  if(tp==1){
    this.apitype="idols"
    this.title="美女视频"
    this.contentType=1;
  }
  else if(tp==2){
    this.apitype="videos"
    this.title="欧美热片"
    this.contentType=1;
  }
  else if(tp==3){
    this.apitype="mmlists"
    this.title="美女写真"
    this.contentType=3;
  }
  else if(tp==4)
  {
    this.apitype="books"
    this.title="漫画"
    this.contentType=4;

  }
  else if(tp==5)
  {
    this.apitype="vip/china"
    this.title="国产 HD"
    this.contentType=1;
  }
  else if(tp==6)
  {
    this.apitype="vip/japan"
    this.title="日韩 HD"
    this.contentType=1;
  }
  else if (tp==7)
  { 
    this.apitype="lives1"
    this.title="直播1"
    this.contentType=2;
  }
  else if (tp==8)
  { 
    this.apitype="lives2"
    this.title="直播2"
    this.contentType=2;
  }
  else if (tp==9)
  { 
    this.apitype="movies"
    this.title="成人hd"
    this.contentType=1;
  }
  else if (tp==10)
  { 
    this.apitype="korean"
    this.title="韩国主播"
    this.contentType=1;
  }
  this.page=1

  this.loadMore(false)
},
currentType(tp){
  return this.apitype.indexOf(tp)>0
}
}
}
</script>

